import React, { useEffect, useState, useRef } from "react";
import { Container, Row } from "react-bootstrap";
import { Link, useNavigate } from "react-router-dom";
import data from "./data.js";
import styled from "styled-components";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

const NextArrow = ({ onClick }) => {
  // props onClick  .
  return (
    <button onClick={onClick} type="button" className="nextBtn">
      {">"}
    </button>
  );
};
const PrevArrow = ({ onClick }) => {
  // props onClick  .
  return (
    <button onClick={onClick} type="button" className="prevBtn">
      {"<"}
    </button>
  );
};

function Front(props) {
  const navigate = useNavigate();

  //
  const settings = {
    nextArrow: <NextArrow />,
    prevArrow: <PrevArrow />,
    dots: false, //    
    infinite: true, //  
    speed: 1000,
    autoplay: true,
    autoplaySpeed: 2000, //  
    slidesToShow: 3, // 4 
    slidesToScroll: 1, // 1  
    centerMode: true,
    centerPadding: "0px",
    arrow: true,
  };

  //----------------
  //     

  const [px, setPx] = useState(0);
  useEffect(() => {
    const copy = [...data[`data${props.i + 1}`]];
    props.setDogFood(copy);
  }, [props.i, props.setDogFood, px]);

  const hanleClick = (e) => {
    props.setKvalue(e);
  };
  const tClick = (e) => {
    props.setDtitle(e);
  };
  const pClick = (e) => {
    props.setDprice(e);
  };
  //let navigate = useNavigate();

  let frontRabel = [
    "  ",
    "    ,  !",
    "      ",
    "    !",
    " ,   !",
  ];
  return (
    <>
      <Container>
        <h5 className="front-food1">{frontRabel[props.i]}</h5>
        <div className="front-imgBox">
          <Slider {...settings}>
            {data[`data${props.i + 1}`].map((item, j) => {
              return (
                <div
                  key={item.id}
                  onClick={() => {
                    hanleClick(props.i + 1);
                    navigate(`/food${props.i + 1}/detail${item.id}`);
                    props.setJvalue(item.id);
                    pClick(item.price);
                    tClick(item.name);
                  }}
                  className="front-imgInner"
                >
                  <img
                    src={
                      process.env.PUBLIC_URL + `/${props.i + 1}dog${j + 1}.png`
                    }
                  />
                  {/* <img src={`${props.i + 1}dog${j + 1}.png`} alt="frontImg" /> */}
                  <h4>{item.name}</h4>
                  <p>{item.price}</p>
                </div>
              );
            })}
            {/* </div> */}
          </Slider>
        </div>
      </Container>
    </>
  );
}

export default Front;
