import { Container, Row, Button } from "react-bootstrap";
import { pushBtn } from "../store.js";
import { useSelector, useDispatch } from "react-redux";
import { changeCnt, sumPrice } from "../store.js";

function TabChange(props) {
  const dt = props.dt;
  const jValue = props.jValue;
  const dp = props.dp;
  const kValue = props.kValue;
  let stock = useSelector((state) => state.stockData);
  let dispatch = useDispatch();
  if (props.tab === 0) {
    return (
      <div className="tabBox">
        <div className="tabBoxInner">
          <h3>  .</h3>

          <div className="tabBoxImg">
            <img
              src={
                process.env.PUBLIC_URL +
                `/${props.kValue}dog${props.jValue}.png`
              }
              alt="aa"
            />
            <div className="tabBoxTwo">
              <h5>:{props.dt}</h5>
              <h5>:{props.dp}</h5>
              <Button
                className="detailBtn"
                onClick={() => {
                  console.log(dp);
                  let idx = 0;
                  if (stock.length == 0) {
                    dispatch(
                      pushBtn({
                        id: jValue,
                        name: dt,
                        price: dp,
                        copy: dp,
                        cnt: 1,
                      })
                    );
                  } else if (stock.length != 0) {
                    for (var i = 0; i < stock.length; i++) {
                      if (stock[i].name == dt) {
                        dispatch(changeCnt(i));
                        idx++;
                      }
                    }
                    if (idx == 0) {
                      dispatch(
                        pushBtn({
                          id: jValue,
                          name: dt,
                          price: dp,
                          copy: dp,
                          cnt: 1,
                        })
                      );
                    }
                  }
                }}
                variant="warning"
              >
                
              </Button>
            </div>
          </div>
          <h5>
                    
             . 
          </h5>
          <h5>
                    
            .
          </h5>

          <h5>
                     
            .
          </h5>

          <h5>
                     
             .
          </h5>
          <h5>
                 .   
             .
          </h5>
        </div>
      </div>
    );
  }
  if (props.tab === 1) {
    return (
      <div className="tabBox">
        <div className="tabBoxInner">
          <h3>  </h3>
          <h5>
               .
            <br />      , <br />{" "}
                 .
          </h5>
          <h5>   </h5>
          <h5>
                1~3  ,
            <br />         .
            <br />      ,
            <br />    SMS   .
          </h5>
          <h5>  </h5>
          <h5>
                 .
            <br />         
            .
            <br />         
              .
          </h5>
          <h5>   </h5>
          <h5>
                 ,
            <br />      .
            <br />  , ,  ,    
            <br />    .
          </h5>
          <h5>  </h5>
          <h5>
                  
            .
            <br />    .
          </h5>
          <h5>
                    
            . ! 
          </h5>
        </div>
      </div>
    );
  }
  if (props.tab === 2) {
    return (
        <div className="tabBox">
          <div className="tabBoxInner">
            <div className="starBox">  .</div>
          </div>
        </div>
    );
  }
}

export default TabChange;
